body{
	margin: 0;
	padding: 0;
	min-width: 320px;
	background-color: #ffffff;
}

img{
	max-width: 100%;
	height: auto;
}
svg{
	max-width: 100%;
	height: auto;
}
// Контейтер

.container{

	padding-left: 4%;
	padding-right: 4%;
	box-sizing: border-box;
	// box-sizing: border-box;
}


@media(min-width: 767px){
	.container{
		
		padding-left: 8%;
		padding-right: 8%;
		max-width: 1280px;
		margin: 0 auto;
	}
	
}
@media(min-width: 1280px){
	.container{
		
		padding-left: 8%;
		padding-right: 8%;
		max-width: 1920px;
		margin: 0 auto;
	}
	
}

@media(min-width: 1919px){
	.container{
		
		padding-left: 6%;
		padding-right: 6%;
		max-width: 1920px;
		margin: 0 auto;
	}
	
}

.red-accent{
	color: $red-color;
}

[data-scroll] {
	will-change: transform;
}
.parallax__item{
	position: relative;
	overflow: hidden;
}

.parallax__img-wrap{
	position: absolute;
	height: 100%;
	width: 100%;
	will-change: transform;
	
}


	.parallax__img {
		--overflow: 20%;
		height: calc(100% + (2 * var(--overflow)));
		top: calc( -1 * var(--overflow));
		width: 100%;
		position: absolute;
	
		// background-image: var(--image);
		// background-size: cover;
		// background-position: 50% 0%;
		will-change: transform;
	}

	@media(min-width: 1280px){
		.parallax__img{
			--overflow: 30%;
		}
		
	}
	